@import url(https://use.fontawesome.com/releases/v5.5.0/css/all.css);
/* JORGE WUZ HERE */

/* Icons */

/* Reset */
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* Form styles */
.login-container {
  width: 280px;
  color: white;
  margin: 0 auto;
}

.bg-container {
  background: url(/front-end/static/media/sunset.76874532.png) no-repeat;
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  font-family: sans-serif;
  background-color: red;
}

.login-container h1 {
  float: left;
  font-size: 40px;
  border-bottom: 6px solid #03071e;
  margin-bottom: 35px;
  padding: 13px 0;
}

/* Input styles */
.input-container {
  width: 100%;
  overflow: hidden;
  font-size: 20px;
  padding: 8px 0;
  margin: 8px 0;
  border-bottom: 1px solid#03071e;
}

.input-container i{
  width: 26px;
  float: left;
  text-align: center;
  color:white;
}

.input-container input {
  border: none;
  outline: none;
  background: none;
  color: white;
  font-size: 18px;
  width: 80%;
  float: left;
  margin: 0 10px;
}

::-webkit-input-placeholder {
  color: white;

  /* Necessary for Firefox */
  opacity: 1; 
}

:-ms-input-placeholder {
  color: white;

  /* Necessary for Firefox */
  opacity: 1; 
}

::-ms-input-placeholder {
  color: white;

  /* Necessary for Firefox */
  opacity: 1; 
}

::placeholder {
  color: white;

  /* Necessary for Firefox */
  opacity: 1; 
}

.role {
  display: flex;
  padding: 5px;
  margin: 12px 0;
}
.role label {
  width: 40%;
}

/* Button styles */
#submitBtn {
  width: 100%;
  background: none;
  border: 1px solid #03071e;
  color: #03071e;
  padding: 5px;
  font-size: 18px;
  cursor: pointer;
  margin: 12px 0;
}

/* Fonts */

.market-container {
  font-family: Helvetica, Arial,sans-serif;
}

.market-container h1 {
  font-size: 2rem;
}

.market-container h1 h2, h3 {
  margin: 0.5rem 0;
}

.market-container h2 {
  font-size: 1.5rem;
}

/* Positioning */

.row {
  display: flex;
  justify-content: space-between;
}

.col-1 {
  flex: 1 1;
}

.col-2 {
  flex: 2 1;
}

.block {
  background-color: #e0e0e0;
  padding: 1rem;
  margin: 1rem;
  border-radius: 0.5rem;
  flex-flow: wrap;
}

.row-center {
  align-items: center;
}

/* Market items */

ul {
  list-style-type: none;
}

/* Product Styles */
.product img{
  max-height: 8rem;
}

/* Button styles */
.btn {
  font-size: 0.8rem;
  padding: 0.2rem;
  margin: 0.1rem;
  border-radius: 1rem;
  border: 0.1rem #404040 solid;
  background-color: #f0c040;
  width: 100%;
  cursor: pointer;
  font-weight: bold;
  margin-top: 10px;
}

/* Button State Styles */
button.add {
  background-color: #40c0f0;
  width: 1.5rem;
}

button.delete {
  background-color: #f04040;
  width: 1.5rem;
}

button.badge {
  background-color: #f04040;
  border: none;
  color: #fff;
  width: 1.5rem;
}

.text-right {
  text-align: right;
}
